import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Col, Row } from 'react-bootstrap';

import Meta from './Meta';
import Header from '../components/Header/Header';

import Footer from '../components/Footer/Footer';
import styled, { ThemeProvider, createGlobalStyle } from 'styled-components';

const theme = {
  red: '#FF0000',
  black: '#000000',
  grey: '#999999',
  lightgrey: '##f7f7f7',
  offWhite: '#EDEDED',
  White: '#ffffff',
  maxWidth: '1000px',
  bs: '0 12px 24px 0 rgba(0, 0, 0, 0.09)'
};

const GlobalStyle = createGlobalStyle`
  .btn {
    cursor: pointer;
    color: rgb(255, 255, 255);
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 14px;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    max-width: 100%;
    background: #ff3366;
    border-width: 2px;
    border-style: solid;
    border-color: #ff3366;
    border-image: initial;
    border-radius: 100em;
    outline: 0px;
    overflow: hidden;
    padding: 13px 32px 11px;
    text-decoration: none;
    transition: all 0.2s ease 0s;
  }
  .btn:hover {
    color: #e81e61;
    background: #ffffff;
    border-color: #e81e61;
  }
`;

const Layout = props => (
  <div>
    <Meta />
    <Header />
    {props.children}
    <Footer />
    <style jsx>{`
      body {
        background: #ffffff;
      }
      .mainContainer {
        padding: 0px;
        border: 0px;
        width: calc(100% - 60px);
        height: 100%;
        max-width: 1740px;
        position: relative;
        display: block;
        margin: 0px auto;
      }

      @media only screen and (min-width: 1755px) {
        .mainContainer {
          margin: 0px auto;
        }
      }
    `}</style>
  </div>
);

export default Layout;
